import { graphql, PageProps } from "gatsby";

import { useTranslation } from "gatsby-plugin-react-i18next";

import Layout from "../../components/layout";
import Seo from "../../components/seo";
import Schedule from "../../components/pages/schedule";

import useEditionData from "../../hooks/useEditionData";

const SchedulePage: React.FC<PageProps> = ({ location }) => {
  const { t } = useTranslation();

  const editionData = useEditionData(2022);

  return (
    <Layout>
      <Seo title={t("title")} pathname={location.pathname} />

      <Schedule editionData={editionData} />
    </Layout>
  );
};

export default SchedulePage;

export const query = graphql`
  query ($language: String!) {
    locales: allLocale(
      filter: {
        ns: { in: ["common", "schedule"] }
        language: { eq: $language }
      }
    ) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`;
